.testimonial-block {
	padding-top: 150px;
	@include lg {
		padding-top: 78px; }
	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__list {
		padding-top: 70px;
		@include md {
			padding-top: 40px; } }
	.col-4 {
		@include md {
			&:not(:first-child) {
				margin-top: 16px; } } }
	&__btn {
		@include md {
			display: none; }
		&_mob {
			display: none;
			.btn {
				min-width: 288px; }
			@include md {
				display: block;
				text-align: center;
				margin-top: 25px; } } }
	.slick-dots {
		display: flex;
		align-items: center;
		padding-top: 10px;
		li {
			&:not(:first-child) {
				margin-left: 6px; } } } }
